import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Home, Ship, Globe, Package, Phone } from "lucide-react";

const navItems = [
  { icon: Home, label: "Home", href: "#home" },
  { icon: Ship, label: "Services", href: "#services" },
  { icon: Globe, label: "Why Us", href: "#why-us" },
  { icon: Package, label: "Gallery", href: "#gallery" },
  { icon: Phone, label: "Contact", href: "#contact" },
];

export function Sidebar() {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <>
      {/* Toggle Button */}
      <Button
        variant="outline"
        size="icon"
        className="fixed top-4 left-4 z-50"
        onClick={() => setIsOpen(!isOpen)}
      >
        <span className="sr-only">Toggle Menu</span>
        {isOpen ? (
          <Package className="h-5 w-5" />
        ) : (
          <Home className="h-5 w-5" />
        )}
      </Button>

      {/* Sidebar */}
      <div
        className={`fixed inset-x-0 top-0 transform ${
          isOpen ? "translate-y-0" : "-translate-y-full"
        } h-64 bg-white shadow-lg transition-transform duration-300 z-40`}
      >
        <ScrollArea className="h-full py-6 px-6">
          <div className="space-y-4">
            {navItems.map((item, index) => (
              <a
                key={index}
                href={item.href}
                className="flex items-center gap-3 rounded-lg px-3 py-2 text-gray-500 transition-all hover:text-gray-900 hover:bg-gray-100"
                onClick={() => setIsOpen(false)}
              >
                <item.icon className="h-5 w-5" />
                {item.label}
              </a>
            ))}
          </div>
        </ScrollArea>
      </div>
    </>
  );
}
